.form {
  display: flex;
  flex-direction: column;

  width: 300px;
  height: 260px;
  margin: 0 auto;
  background-color: white;
  padding: 20px;
  border: 1px solid rgba(16, 223, 126, 0.3);
  border-radius: 10px;
}

.input {
  display: block;
  margin-bottom: 15px;
  width: 100%;
  border-radius: 10px;
}

.input:invalid {
  border: red solid 3px;
}
